import React from "react";
import { NavLink } from "react-router-dom";
import { useDeviceDetect } from "../../hooks";
import { useShoppingCartStore } from "../../store/shoopingCartStore";

const MiniShoppingCart = () => {
  const { sc, totalPrice } = useShoppingCartStore();
  console.log("mini SHOPPING CART RENDER");
  const soppingCartNumber = sc.length;

  const navLinkClassName = (navData) => {
    return navData.isActive ? "navLink miniSC Active" : "miniSC navLink";
  };

  const { isMobile } = useDeviceDetect();
  return !isMobile ? (
    <div className={"navLink miniSC"}>
      <div>
        <div>Cart</div>
        <div>{soppingCartNumber > 0 ? `${soppingCartNumber} Items` : ""}</div>
      </div>
      <div className="preview">
        <div>Items: {soppingCartNumber}</div>
        <div>Total Price: ${totalPrice}</div>
        <NavLink
          className={(navData) => navLinkClassName(navData)}
          to="/shoppingcart"
        >
          Go to shopping cart Page
        </NavLink>
      </div>
    </div>
  ) : (
    <NavLink
      className={(navData) => navLinkClassName(navData)}
      to="/shoppingcart"
    >
      <div>
        <div>Cart</div>
        <div>{soppingCartNumber > 0 ? `${soppingCartNumber} Items` : ""}</div>
      </div>
    </NavLink>
  );
};

export default React.memo(MiniShoppingCart);
